body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url('./assets/young-man-sportswear-exercise-class-gym.jpg');
  background-size:cover;                    
  background-repeat:   no-repeat;
  background-attachment: fixed;
  background-position: center center;
}

.workoutArticle{
  box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
  border: thick;
  border-color: rgba(0, 0, 0, 0.60);
  border-style: solid;
  border-width: 2px;
  background-color: rgba(50, 50, 50, 0.85);
  border-radius: 20px;
  max-width: 500px;
}

body button:hover{
  color: rgba(0, 0, 0, 0.60);
  background-color: 		azure;
  transition-duration: 0.4s;
} 

.Loading{
  text-align: right;
  color: azure;
}

#header{
  color: #fff;
  background-color: rgba(50, 50, 50);
  padding: 30px 30px 5px 30px;
  font-weight: 300;
  font-size: 32px;
  font-style: italic;
  margin: 0;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

.headernavbar{
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: rgba(40, 40, 40);
  box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
  position: sticky;
}

.headernavbar li {
  float: left;
}

.headernavbar a{
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.headernavbar a:hover{
  background-color: #111;
  cursor: pointer;
}

/*SINGLEWORKOUT*/
.singleWorkout{
  color: azure;
}

.singleHead{
  margin: 0;
  display: flex;
  justify-content: center;
  font-weight: 200;
  font-size: 22px;
  text-decoration: underline;
  text-underline-offset: 0.1em;
}

.themeDate{
  display: grid;
  justify-content: space-around;
  grid-template-columns: auto auto auto;
}

.themeDate h2{
  font-weight: 200;
  font-size: 26px;
}

/*MOVEMENTS*/
.movementsUl{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-auto-flow: column;
  list-style: none;
}

#movementName{
  font-size: 18px;
  font-weight: 200;
}

#movementSet{
  font-size: 18px;
  font-weight: 200;
}

.weightsUl{
  width: auto;
  list-style: none;
  font-weight: 200;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0.60);
  border-width: 3px;
  border-radius: 2px;
  padding: 2px 10px 2px 10px;
}
.weightsUl li{
  text-align: center;
}
/*MOVEMENTS*/

/*CARDIO*/
.cardioUl{
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 2.5fr;
  font-size: 18px;
  font-weight: 200;
}
.cardioDuration{
  text-align: center;
  width:fit-content;
  list-style: none;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0.60);
  border-width: 3px;
  border-radius: 2px;
  padding: 2px 10px 2px 10px;
}
/*CARDIO*/

.bottomButton{
  text-align: center;
  padding-bottom: 2px;
}
.bottomButton button{
  width: fit-content;
  cursor: pointer;
  border:none;
  background-color: rgba(0, 0, 0, 0.60);
  color: azure;
  padding: 3px;
  border-radius: 5px;
  font-weight: 200;
}
/*SINGLEWORKOUT*/

/*HISTORY*/
.historySection{
  color: azure;
}

.historySection h2{
  font-weight: 200;
  font-size: 24px;
  text-align: center;
}

.historySection nav {
  height: auto;
  max-height: 500px;
  overflow-y: scroll;
  scrollbar-width: thin;
  border-width: 1px 0px 1px 0px;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0.60);
}

.historySection ul{
  list-style-type: none;
  text-align: center;
  margin: 0 auto;
  padding: 0px;
  overflow: hidden;
  border-width: 0px 0px 1px 0px;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0.60);
}

.historySection li{
  float: left;
  padding: 2px;
  width: 150px;
  margin-left: auto ;
  margin-right: auto ;
}

.linktoworkout{
  color: azure;
  text-decoration: none;
}

.linktoworkout:hover{
  color: #111;
  transition-duration: 0.2s;
}
/*HISTORY*/


/*ADDWORKOUT*/
.addWorkout{
  color: azure;
}
.addWorkout h2{
  text-align: center;
  font-weight: 200;
  font-size: 24px;
  padding: auto;
}

.addWorkout p{
  margin: 0;
  background-color:#222;
  padding: 2px;
  font-weight: 200;
  font-size: 18px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.60);
  border-right: 1px solid ;
  text-align: center;
}

.addWorkout input{
  border-style: none;
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.60);
  padding: 2px;
  background-color: #111;
  color: #fff;
}

/*THEMEDATEDURATION*/
.infoadd{
  text-align: center;
}

.infoadd ul{
  list-style: none;
  margin: 0;
  padding: 0;
}

.infoadd li{
  font-weight: 200;
  font-size: 18px;
  margin: 2px;
}

.infoadd select{
  font-weight: 200;
  margin: 2px;
  padding: 3px;
  width: 180px;
  text-align: center;
  background-color: #111;
  border-style: none;
  color: azure;
}

.infoadd select:hover{
  cursor: pointer;
}

.inputtime {
  margin-left: auto;
  margin-right: auto;
  width: 100px;
  border: none;
  color: azure;
  text-align: center; 
}

.datepicker{
  padding: 4px !important;
  text-align: center;
  border-style: none;
}
/*THEMEDATEDURATION*/

/*MOVEMENT*/
.movementadd{
  margin-left: auto;
  margin-right: auto;
  border: 1px solid rgba(0, 0, 0, 0.60);
  width: 350px;
}

.movementadd div {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
/*MOVEMENT*/

/*CARDIO*/
.cardioadd{
  margin-left: auto;
  margin-right: auto;
  border: 1px solid rgba(0, 0, 0, 0.60);
  width: 350px;
  margin-bottom: 10px;
}

.cardioadd div {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
/*CARDIO*/

.savebutton{
  display: grid;
  grid-template-columns: auto !important;
  
}

.savebutton button{
  cursor: pointer;
  border:none;
  background-color: rgba(0, 0, 0, 0.60);
  color: azure;
  padding: 3px;
  font-weight: 200;
}
/*ADDWORKOUT*/

/*BEINGADDED*/

.movementaddedUl{
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 0.5fr;
}
.cardioaddedUl{
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
.deleteSinglebutton{
  cursor: pointer;
  border:none;
  background-color: rgba(0, 0, 0, 0.60);
  color: azure;
  padding: 3px;
  font-weight: 200;
  margin-left: auto;
  margin-right: auto;
}
.beingaddedarticle li{
  font-weight: 200;
  font-size: 18px;
  margin: 2px;
}

@media (min-width: 900px){
  body{
    height: 100vh;
  }
  .workoutArticle{
    overflow: hidden;
    margin: 30px auto 0 auto;
  }
}